/* ==============================
   BASE STYLES
   ============================== */
/************************************

             Media queries

 To use these, put this snippet in the appropriate selector:

     @include bp(tiny) {
         background-color: purple;
     }

     Replace "tiny" with "medium" or "big" as necessary.
*************************************/
body {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  display: inline-block;
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  margin: 8px 0px;
}
@media screen and (max-width: 600px) {
  h1 {
    margin-top: 0;
  }
}

h2 {
  font-size: 24px;
  line-height: 30px;
  font-weight: 500;
  margin: 32px 0px 24px;
  clear: both;
}

h3 {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  margin: 24px 0px 12px;
  clear: both;
}

h4 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  margin: 8px 0px;
  clear: both;
}

h5 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin: 8px 0px;
  clear: both;
}

h6 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin: 8px 0px;
  clear: both;
}

h2 a, h3 a, h4 a, h5 a, h6 a {
