}

strong {
  font-weight: 600;
}

table {
  border-collapse: collapse;
  border-radius: 2px;
  border-spacing: 0;
  margin: 12px 0 32px;
}

table tbody th {
  max-width: 100px;
  padding: 13px 32px;
  text-align: left;
}

td {
  font-weight: 400;
  padding: 8px 30px;
  letter-spacing: 0.3px;
}
td p {
  margin: 0;
}

th {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  padding: 13px 32px;
  text-align: left;
}

p > code, li > code, td > code, th > code {
