  font-size: 85%;
  letter-spacing: 0;
  line-height: 1;
  padding: 2px 0;
  background-color: #F1F1F1;
  border-radius: 4px;
}

code {
  font-family: "Droid Sans Mono", monospace;
  font-size: 90%;
}

